@use "global" as *;

.price__contents--left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  margin-top: rem(255);
  padding: 0 rem(16) 0;
  width: 100%;
  @include mq(xl) {
    width: rem(492);
    margin-top: rem(40);
    display: flex;
    padding: 0;
    flex-direction: column;
    top: calc(50% + rem(304));
    left: calc(50% - rem(294));
    transform: translate(-50%, -50%);
  }
}
.price__contents--right {
  display: block;
  display: flex;
  flex-direction: column;
  @include mq(xl) {
    display: block;
  }
}

.price__content {
  display: flex;
  justify-content: space-between;
  width: 100%;
  font-family: $base-font-family;
}

.price__img  {
  display: block;
  width: rem(345);
  height: rem(227);
  position: absolute;
  top: calc(50% + rem(140));
  left: calc(50% + rem(0));
  transform: translate(-50%, -50%);
  @include mq(xl) {
    position: absolute;
    top: calc(50% + rem(0));
    left: calc(50% + rem(0));
    transform: translate(-50%, -50%);
    display: block;
    width: rem(492);
    height: rem(746);
  }
}
.price__img img {

  @include mq(xl) {
    position: absolute;
    top: calc(50% + rem(415));
    left: calc(50% + rem(292));
    transform: translate(-50%, -50%);
    display: block;
    width: rem(492);
    height: rem(746);
  }
}

.price__title {
  font-family: $base-font-family;
  font-weight: $bold;
  border-bottom: 1px solid $blue;
  padding-bottom: rem(3);
  margin-top: rem(40);
  @include mq(xl) {
    padding: 0;
    margin-top: 0;
    line-height: rem(24);
    font-size: rem(16);
    padding: rem(39) 0 rem(4);
  }
}

.price__item {
  font-family: $base-font-family;
  font-size: rem(14);
  padding-top: rem(11);
  @include mq(xl) {
    font-size: rem(16);
    line-height: rem(24);
    font-weight: $normal;
    padding: rem(8) 0 0;
  }
}

.price__price {
  font-family: $base-font-family;
  font-size: rem(14);
  padding-top: rem(11);
  @include mq(xl) {
    font-size: rem(16);
    line-height: rem(24);
    font-weight: $normal;
    padding: rem(8) 0 0;
    text-align: right;
  }
}
